export default {
	props: {
		text: String, //显示文本
		rotate: {
			//是否启动加载
			type: [Boolean, String],
			default: false
		},
		bgColor: {
			//按钮背景颜色
			type: String,
			default: 'linear-gradient(to right, rgba(0,0,0,0.7), rgba(0,0,0,0.6))'
		},
		fontColor: {
			//按钮字体颜色
			type: String,
			default: '#FFFFFF'
		}
	},
	computed: {
		_rotate() {
			//处理值
			return String(this.rotate) !== 'false';
		}
	},
	methods:{
		bClick(){
			this.$emit('bClick')
		}
	}
};
